import { IPropsUserData } from "pages/Interface"
import { Link } from "react-router-dom"
import { useNavigate } from "react-router-dom"

export const MobileMenu = ({ userData }: { userData: IPropsUserData }) => {
  const nav = useNavigate()

  const userSignOut = () => {
    localStorage.removeItem("usertoken")
    nav("/")
  }
  return (
    <>
      {
        userData._id ?
          <>
            <Link to={"/user/dashboard/profile"} className="dropdown-item">Profile</Link>
            <Link to={"/user/dashboard/cart"} className="dropdown-item">Checkout</Link>
            <div className="dropdown-item" onClick={userSignOut}>Sign Out</div>
          </>
          :
          <Link to={"/user/login"} className="dropdown-item">Login</Link>
      }
    </>
  )
}